.swiper {
    z-index: 1;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

.swiper-slide {
    z-index: 1;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    display: flex;
    margin: auto;
    width: 50%;
    height: 50%;
    object-fit: cover;
}

.swiper {
    z-index: 1;
    margin-top: -50px;
    margin-bottom: -130px;
    margin-left: 10px;
    width: 100%;
    height: 570px;
    z-index: 2;
    top: -110px;
}

.append-buttons {
    text-align: center;
    margin-top: 20px;
}

.append-buttons button {
    display: inline-block;
    cursor: pointer;
    border: 1px solid #007aff;
    color: #007aff;
    text-decoration: none;
    padding: 4px 10px;
    border-radius: 4px;
    margin: 0 10px;
    font-size: 13px;
}

.swiper-button-next,
.swiper-button-prev {
    top: 41px;
}

.swiper-button-next:after,
.swiper-button-prev:after {
    font-size: 20px;
    z-index: 2;
    font-weight: bold;
    color: #3579f8;
    min-width: 40px;
    height: 40px;
    position: absolute;
    border-radius: 50%;
    z-index: 1;
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(8px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-button-prev {
    right: 70px;
    left: auto;
}

.swiper-button-next {
    right: 20px;
}

@media screen and (max-width:630px) {

    .swiper-button-next,
    .swiper-button-prev {
        top: 510px;
    }

    .swiper-button-next {
        right: 40%;
    }

    .swiper-button-prev {
        right: 50%;
    }
}

@media screen and (max-width:500px) {
    .swiper-button-next {
        right: 38%;
    }

    .swiper-button-prev {
        right: 51%;
    }
}